<template>
  <div id="app">
    <component :is="layout">
      <router-view></router-view>
    </component>
  </div>
</template>

<script>
import EmptyLayout from '@/layouts/EmptyLayout';
export default {
  computed: {
      layout() {
     
        return (this.$route.meta.layout || 'empty') + '-layout'; 
    
      }
    
  },
  components: {
    EmptyLayout,
  }
};
</script>

